import * as React from 'react';
import { graphql } from 'gatsby';
import { Tag as TagData } from '~src/templates/__generated__/Tag';
import Layout from '~src/components/Layout/Layout';
import useIntlHelper from '~src/i18n/helpers/useIntlHelper';
import HeaderBackground from '~src/components/Layout/Header/HeaderBackground';
import { PostFeed } from '~src/components/Sections/PostFeed/PostFeed';
import styles from './tag.module.scss';
import { FormattedMessage } from 'react-intl';
const TagHero = ({ data }) => {
	const { formatMessage } = useIntlHelper();
	return (
		<header className="site-archive-header">
			<HeaderBackground fluidImg={data.tag.image.childImageSharp.fluid}>
				<div className={`"inner " + ${styles.archiveHeaderContent}`}>
					<h1 className="site-title">{formatMessage(`tags.${data.tag.id}.title`)}</h1>
					<h2 className="site-description">
						{(data.tag.description && formatMessage(`tags.${data.tag.id}.desc`)) || (
							<FormattedMessage id="posts.collection" values={{ num: data.posts.nodes.length }} />
						)}
					</h2>
				</div>
			</HeaderBackground>
		</header>
	);
};
const TagTemplate: React.FC<{ data: TagData }> = ({ data }) => {
	return (
		<>
			<Layout template="tag" data={data} header={<TagHero data={data} />}>
				<div className="inner">
					<PostFeed data={data} />
				</div>
			</Layout>
		</>
	);
};

export default TagTemplate;

export const TagTemplateQuery = graphql`
	query Tag($id: String!, $locale: String!) {
		tag: tagsYaml(id: { eq: $id }) {
			description
			image {
				childImageSharp {
					fluid {
						...GatsbyImageSharpFluid_withWebp_tracedSVG
					}
				}
			}
			id
		}
		posts: allMdx(filter: { fields: { locale: { eq: $locale } }, frontmatter: { tags: { in: [$id] } } }) {
			nodes {
				fields {
					slug
				}
				frontmatter {
					date(formatString: "DD MMMM YYYY")
					title
					tags
					category
					image {
						childImageSharp {
							fluid {
								...GatsbyImageSharpFluid_withWebp_tracedSVG
							}
						}
					}
				}
				excerpt(pruneLength: 200, truncate: true)
				timeToRead
			}
		}
	}
`;
